<template>

    <!-- MODAL PARA NUEVO REGISTRO  -->
 <button type="button" id="modalusuarioAlta" class="btn btn-primary" data-toggle="modal" data-target="#nuevo" data-whatever="@mdo">Nuevo Usuario</button>  
<div class="modal fade" id="nuevo"  data-backdrop="static" data-keyboard="false"  aria-labelledby="exampleModalLabel" aria-hidden="true">
  <div class="modal-dialog">
    <div class="modal-content">
      <div class="modal-header">
        <h5 class="modal-title" id="exampleModalLabel">Nuevo Usuario</h5>
        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
          <span aria-hidden="true" >&times;</span>
        </button>
      </div>
      <div class="modal-body">
        <form>
         <div class="form-group">
    <label for="exampleFormControlInput1">cuenta</label>
    <input  type="email" class="form-control" id="exampleFormControlInput1" placeholder="Correo Electronico" v-model="cuenta">
  </div>
   <div class="form-group">
    <label for="exampleFormControlInput1">Contraseña</label>
    <input type="text" class="form-control" id="exampleFormControlInput1" placeholder="Contraseña" v-model="pass">
  </div>
  <div class="form-group">
    <label for="exampleFormControlInput1">Nombre y Apellido</label>
    <input type="text" class="form-control" id="exampleFormControlInput1" placeholder="Nombre y Apellido" v-model="nombreApe">
  </div>
 
      <label for="rol">Roles</label>
        <select v-model="rol" class="form-control" id="rol">
            <option v-for = 'rol in roles' :key="rol.id" :value="rol.id">{{rol.nombre}}</option>
        </select>
  
        </form>
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-secondary" data-dismiss="modal">Volver</button>
        <button type="button" @click='Guardar()'  class="btn btn-primary">Guardar</button>
      </div>
    </div>
  
<!--fin del MOdal-->
</div>
</div>
 
</template>

<script>
import axios from "axios";
export default {
    data(){
        return{
               usuario:[],
               roles: [],
             
               cuenta:null,
               pass:null,
               nombreApe:null,
               rol:null,
        }
    },
     mounted(){
        
         this.cargarRol();/// carga los roles en el select
        
         
     },
   methods:{
        async Guardar(){
      let post = {
                
                cuenta: this.cuenta,
                pass: this.pass,
                nombre:this.nombreApe,
                rol_id:this.rol
  
  }

   console.log(post);
  
     try{ 
       
 const result= await axios.post('http://localhost:3000/usuarios/', post)
    console.log(result)
    if(result.data=="Usuarios existente"){
      this.$swal({icon:'error',
                       title: 'Usuario existente',
                       button:'OK' })
    }else{
           if(result.data=='Usuario Creado'){
             this.$swal.fire({icon:'success',
                       title: 'Datos guarda Correctamente',
                       button:'OK' })
                    this.cuenta="",
                    this.pass="",
                    this.nombreApe="",
                    this.rol=0
      
          /// cerrar modal ////
              const cerrarmodal= document.getElementById("modalusuarioAlta");
              cerrarmodal.click()
           } 
              if(result.data=="Verifique Rol"){
                     this.$swal({icon:'error',
                       title: 'Verifique Rol',
                       button:'OK' })

                }
               
        


    }  
     

                

     }catch(error){
         console.log(error)
     }
     
  },
cargarRol:async function(){
         try{
           const roles = await axios.get('http://localhost:3000/roles')
           if(roles.data.message== 'no se encontraron roles'){
                    this.$swal({icon:'error',
                               title: 'No se encontraron roles',
                               button:'OK' })
                               /*this.roles.push('no hay roles en la base')*/
                                return
             }
             this.roles= roles.data
         }catch(error){

             console.log(error)

         }
         
         
         },

        
         
         
}

}
</script>

